.wrap-footer {
  max-width: 1170px;
  margin: auto;
}

.ground-floor {
  background: #262626;
  margin-top: 100px;
  padding-top: 50px;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: normal;

  &__logo {
    display: block;
    width: 191px;
    height: 20px;
    background-image: url(./../images/white-logo.svg);
    margin-bottom: 50px;
  }

  hr {
    opacity: 0.4;
  }
}

.middle-row {
  text-align: center;
  img {
    transition: transform 0.1s;

    &:hover {
      transform: scale(1.1);
    }

    &:first-child {
      margin: 40px 20px;
    }

    &:last-child {
      margin: 40px 20px;
      border-radius: 7px;
    }
  }
}

.top-row {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  &__aside {
    list-style: none;
    width: 33%;
    box-sizing: border-box;
    padding-top: 40px;
    font-size: 13px;
    line-height: 15px;
    color: #fff;
    height: 220px;
    transition: color 0.3s;
    &-li {
      margin-bottom: 23px;
      &:hover {
        color: gray;
        cursor: pointer;
      }
    }
  }

  &__aside form {
    width: 228px;
    height: 44px;
    margin-bottom: 15px;
  }

  &__aside img:last-child {
    display: block;
  }
}

.form-select {
  position: relative;

  &__world {
    transition: transform 0.5s;
  }
  &__eng {
    transition: opacity 1s;
  }

  &:hover .form-select__world {
    transform: scaleX(-1);
  }

  &__language {
    width: 227px;
    height: 40px;
    margin-bottom: 20px;
    position: relative;
    opacity: 0;
    z-index: 2;
  }

  &__language:focus {
    outline: none;
  }

  &::before {
    content: "";
    width: 227px;
    height: 41px;
    top: -1px;
    left: -1px;
    border-radius: 6px;
    border: 1px solid #2b2b2b;
    position: absolute;
    z-index: 0;
    display: block;
    background-image: url(./../images/select.jpg);
    background-repeat: no-repeat;
    background-position: center;
  }

  &::after {
    content: url(./../images/shape1.png);
    top: 12px;
    left: 203px;
    position: absolute;
    z-index: 1;
    display: block;
  }

  img:first-child {
    position: absolute;
    top: 10px;
    left: 18px;
    width: 20px;
    z-index: 3;
  }

  img:not(:first-child) {
    position: absolute;
    left: 60px;
    top: 14.2px;
    z-index: 3;
  }
}

.socials-links {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 92px;
  height: 60px;
  margin-left: 35px;

  &__ubereats {
    display: flex;
    width: 100%;
    height: 15px;
    transition: transform 0.3s,
      color 0.3s;
    font-size: 16px;
    line-height: 19px;
    margin-bottom: 10px;
    &-hash {
      color: green;
    }
    &:hover .socials-links__ubereats-hash {
      color: rgb(138, 52, 52);
    }
  }

  & a {
    transition: transform 0.3s;
    &:hover {
      transform: scaleX(-1);
    }
  }
}

.bottom-row {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  position: relative;
  color: white;
  align-items: center;
  font-size: 11px;
  line-height: 13px;
  &__link {
    transition: color 0.3s;
    text-decoration: none;
    color: white;
    &:hover {
      color: gray;
      cursor: pointer;
    }
  }

  a:last-child::after {
    content: "";
    width: 100%;
    height: 100%;
    top: 0;
    position: absolute;
    z-index: 22222;
    display: block;
    background-image: url(./../images/shape.png);
    background-repeat: no-repeat;
    background-position: center;
  }

  a:last-child {
    position: relative;
    bottom: 10px;
    right: 0;
    transition: transform 0.5s;
    height: 80px;

    &:hover {
      -webkit-transform: scale(1.05);
      -ms-transform: scale(1.05);
      transform: scale(1.05);
    }
  }
}

@media screen and (max-device-width: 640px) {
  .ground-floor {
    padding: 56px 8px 40px;
    height: auto;
    &__logo {
      margin: auto;
      margin-bottom: 50px;

    }
  }

  .top-row__aside {
    width: 100%;
    height: auto;
    padding-top: 20px;
    & form {
      margin: auto;
      margin-bottom: 20px;

    }
    &:last-child {
      padding-bottom: 20px;
    }
  }

  .bottom-row {
    flex-direction: column;
    align-items: baseline;
    height: 200px;
    justify-content: space-evenly;

    & a:last-child {
      position: absolute;
      top: 90px;
      right: 16px;
    }
  }
}

@media screen and (min-device-width: 641px) and (max-device-width: 800px) {
  .main-head {
    height: 130px;
  }

  .wrap {
    height: 130px;
    justify-content: space-around;
  }

  .main-head__logo {
    order: -2;
  }

  .main-head__buttons-wrap {
    order: -1;
    margin-bottom: 15px;
  }
  .search {
    width: 90%;
  }
}

@media screen and (max-device-width: 1180px) {
  .ground-floor {
    padding: 0 30px;
    padding-top: 50px;
  }
}
